import * as React from "react"
import { useQuery } from "react-query";
import { getWPGraphQLData } from "../api/my-api";
import wellnessBlogHeaderBg from "../images/wellness-blog-header-bg.webp"
import * as styles from "../styles/wellness-blog.module.scss"

const Blog = () => {

    const WP_GQL_QUERY = `
        query {
            posts {
                nodes {
                    slug
                    title
                    excerpt
                    content
                    link
                    date
                    categories {
                        nodes {
                          name
                        }
                    }
                    featuredImage {
                        node {
                            mediaItemUrl
                        }
                    }
                }
            }
        }
    `;

    const { isLoading, isError, data, error } = useQuery('getHomeContent', () => getWPGraphQLData(WP_GQL_QUERY))

    React.useEffect(() => {
        // console.log('QUERY RESPONE >> ', data)
    }, [data])

    const trimExcerpt = (text: string) => {
        return text.replace(' [&hellip;]', '...')
    }


    return (
        <div className={`page-wrapper`}>
            <section
                className="sub-hero"
            // style={{ backgroundImage: `url(${wellnessBlogHeaderBg})` }}
            >
                <div className="image-wrap">
                    <img src={wellnessBlogHeaderBg} alt="Wellness Blog Header BG Image" />
                </div>
                <article>
                    <h1 className="text-3xl font-bold underline">
                        AFC Wellness Blog
                    </h1>

                    <p>
                        Education is imperative and drives our use of positive methodologies, proven biomechanical techniques, and safe nutritional recommendations to effectively train, motivate and educate our clients.
                    </p>
                </article>
            </section>

            <main className={`main`}>
                <section 
                    className="
                        m-[0_auto]
                        masonry md:masonry-md lg:masonry-lg max-w-7xl
                    ">
                    {
                        data &&
                        data.posts?.nodes.map((post: any, key: any) => {
                            const date = new Date(post.date);

                            return (
                                <article className={`mb-7 ${styles.card}`} key={key}>
                                    <figure>
                                        <img src={post.featuredImage?.node.mediaItemUrl} alt={post.title} />
                                    </figure>

                                    <section>
                                        <header>
                                            <h2>{post.title}</h2>
                                        </header>

                                        <p className={styles.date}>
                                            {date.toLocaleString('default', { weekday: 'short' })} {date.toLocaleString('default', { month: 'short', day: 'numeric' })}, {date.toLocaleString('default', { year: 'numeric' })} - {post.categories.nodes[0].name}
                                        </p>

                                        <p dangerouslySetInnerHTML={{ __html: trimExcerpt(post.excerpt) }} />
                                        {/* <div dangerouslySetInnerHTML={{__html: post.content}}/> */}

                                        <button>
                                            <a href={post.link} target="_blank" rel="noopener norefferer">Read More</a>
                                        </button>
                                    </section>
                                </article>
                            )
                        })
                    }
                </section>
            </main>
        </div>
    )
}

export default Blog